
#app {
  height: 100%;
}
.content {
  margin-top: 60px;
  width: 100%;
  overflow: hidden;
}
.content .info {
  margin: 20px 0;
  width: 100%;
  background-color: #fff;
}
.content .info .container {
  width: 90%;
  background-color: #fff;
  margin: 0 auto;
  box-shadow: 0 0 30px -7px rgba(0,0,0,0.3);
  padding: 5px 0;
  border-radius: 4px;
}
.content .info .pic {
  width: 94%;
  margin: 3%;
}
.content .info .pic img {
  width: 100%;
}
.content .info .desc {
  font-size: 16px;
  font-weight: 400;
  margin: 3%;
  padding: 5% 0;
  width: 80%;
}


