
.header {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  background: #fff;
  text-align: center;
  box-shadow: 0 2px 10px #d8d8d8;
}
.header .menu {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 46px;
          flex: 0 0 46px;
  font-size: 22px;
}
.header .title {
  font-size: 22px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.header .write {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 46px;
          flex: 0 0 46px;
  font-size: 22px;
}
.header .AlShow {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 200px;
  background: rgba(0,0,0,0.6);
  text-align: center;
  line-height: 200px;
  color: #fff;
  font-size: 24px;
  animation: ALert 0.5s;
  -moz-animation: ALert 0.5s;
  -o-animation: ALert 0.5s;
  -webkit-animation: ALert 0.5s;
}
.header .detail {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: rgba(0,0,0,0.3);
  animation: detail 0.5s;
  -moz-animation: detail 0.5s;
  -o-animation: detail 0.5s;
  -webkit-animation: detai 0.5s;
}
.header .detail .slidebar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 66%;
          flex: 0 0 66%;
  width: 66%;
  background: #252525;
}
.header .detail .slidebar .useravatar {
  vertical-align: middle;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 20% 0 10% 0;
}
.header .detail .slidebar .useravatar .avatar {
  width: 100px;
  height: 100px;
  margin: 0 auto;
}
.header .detail .slidebar .useravatar .avatar img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #404040;
  margin: 0 auto;
}
.header .detail .slidebar .useravatar .names {
  font-size: 20px;
  color: #fff;
}
.header .detail .slidebar .lists {
  position: relative;
}
.header .detail .slidebar .lists .item {
  padding: 0 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 56px;
  line-height: 56px;
  font-size: 14px;
  color: #707070;
  border-bottom: 1px solid #404040;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  box-sizing: border-box;
  white-space: nowrap;
  padding-left: 20px;
}
.header .detail .slidebar .lists .item .icon-user {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 5%;
          flex: 0 0 5%;
}
.header .detail .slidebar .lists .item .text {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50%;
          flex: 0 0 50%;
}
.header .detail .slidebarRight {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 34%;
          flex: 0 0 34%;
}
@-webkit-keyframes detai {
from {
    width: 0%;
}
to {
    width: 100%;
}
}
@-webkit-keyframes ALert {
from {
    height: 0;
}
to {
    height: 200px;
}
}
@-webkit-keyframes detai {
from {
    width: 0%;
}
to {
    width: 100%;
}
}
@keyframes detai {
from {
    width: 0%;
}
to {
    width: 100%;
}
}
@-webkit-keyframes ALert {
from {
    height: 0;
}
to {
    height: 200px;
}
}
@keyframes ALert {
from {
    height: 0;
}
to {
    height: 200px;
}
}


